	module.exports = "<h2 id=overview>Overview</h2> <p>Use the input group to extend form controls by adding text, buttons, dropdowns, etc.</p> <h2 id=accessibility>Accessibility</h2> <p>When using the <code>pf-c-input-group</code> always ensure labels are used outside the input group with the <code>.pf-u-sr-only</code> class applied. You can also make use of the <code>aria-describedby</code>, <code>aria-label</code>, or <code>aria-labelledby</code> attributues. For more information on accessibility and forms see the <a href=/components/form/examples/ >form component</a>.</p> <table> <thead> <tr> <th>Attribute</th> <th>Applied To</th> <th>Outcome</th> </tr> </thead> <tbody> <tr> <td><code>aria-describedby</code></td> <td><code>pf-c-form-control</code></td> <td>When using <code>.pf-c-input-group__text</code> or <code>.pf-c-input-group__action</code> make use of this on the input field.</td> </tr> </tbody> </table> <h2 id=usage>Usage</h2> <table> <thead> <tr> <th>Class</th> <th>Applied To</th> <th>Outcome</th> </tr> </thead> <tbody> <tr> <td><code>.pf-c-input-group</code></td> <td><code>&lt;div&gt;</code></td> <td>Iniaties the input group. <strong>Required</strong></td> </tr> <tr> <td><code>.pf-c-input-group__text</code></td> <td><code>&lt;span&gt;</code></td> <td>Iniaties the input group text. This can be used to show text, radio, or check boxes.</td> </tr> <tr> <td><code>.pf-c-input-group__action</code></td> <td><code>&lt;button&gt;, &lt;label&gt;</code></td> <td>Iniaties the input group action. This can be used to show action icons such as dismiss, info, or calendar.</td> </tr> <tr> <td><code>.pf-m-align-right</code></td> <td><code>.pf-c-input-group__action</code></td> <td>Aligns the action icon to the right of the input</td> </tr> <tr> <td><code>.pf-m-align-left</code></td> <td><code>.pf-c-input-group__action</code></td> <td>Aligns the action icon to the left of the input</td> </tr> </tbody> </table> ";

